  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.4);
  display: none; }
  #ic-show-img-box-wrap #ic-show-img-box > img {
    display: block;
    width: 100%; }
  #ic-show-img-box-wrap #ic-show-img-info {
    position: absolute;
