.on-ic-popup-show::-webkit-scrollbar {
  display: none; }

[ic-popup] {
  display: none;
  position: fixed;
  z-index: 10001;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
  background: #fff; }

[ic-dropdown] {
  position: relative; }

[ic-dropdown-menu] {
  position: absolute;
  left: 0;
  z-index: 10; }

[ic-dropdown-con] {
  overflow: hidden; }

/* */
[ic-upload] [type=file] {
  position: absolute;
  opacity: 0; }

[ic-accordion] [ic-accordion-con] {
  display: none; }

[ic-accordion] [ic-accordion-item][ic-accordion-active] [ic-accordion-con],
[ic-accordion] [ic-accordion-item]:nth-child(1) [ic-accordion-con] {
  display: block; }

[ic-accordion] [ic-accordion-item][ic-accordion-active] [ic-accordion-title],
[ic-accordion] [ic-accordion-item]:nth-child(1) [ic-accordion-title] {
  display: none; }

[ic-upload][type=file] {
  width: 0;
  height: 0;
  visibility: hidden; }

[ic-tpl] {
  display: none; }

[ic-tpl] [ic-tpl], [ic-tpl-name] [ic-tpl] {
  display: initial; }

[ic-drag-view] {
  position: relative;
  overflow: hidden; }

[ic-role-drag-handle] {
  cursor: move; }

[ic-role-loading] {
  display: none; }

[ic-slider] {
  position: relative;
  overflow: hidden; }

[ic-role-slider-pagination], [ic-select-item], [ic-tab] {
  cursor: pointer; }

[ic-tab-con] {
  display: none; }

/*输入提示列表*/
[ic-role-list] {
  display: none;
  position: absolute;
  z-index: 100; }

/* 对话框 */
[ic-dialog], [ic-prompt] {
  display: none;
  z-index: -2; }

[ic-dialog][ic-active], [ic-prompt][ic-active] {
  z-index: 1000; }

[ic-dialog][ic-active] {
  z-index: 1200; }

[ic-loader] {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center; }

[ic-toggle-target] {
  display: none; }

/*
iOS 上，屏幕是触碰的，所以页面元素默认会有 touch 一类事件，而不是 click。也就是说，
点击一个元素时，iOS 上的 Safari 冒泡的是 touch 一类事件，而不是 click。
只有两类元素，链接与 input 输入域才有 click 事件。
但通过 cursor: pointer 及 onclick= 代码我们又可以让其它元素变得可点击。
这是 iPhone 上的 Safari 有别与其它浏览器的地方。
*/
[ic-view-to], [ic-view-back], [ic-ajax]:not(input[type=text]), [ic-form-submit], [ic-dialog-cancel], [ic-dialog-confirm], [ic-dialog-close], [ic-prompt-close], [ic-date-next-m], [ic-date-prev-m], [ic-click], [ic-clone], [ic-input-add], [ic-input-lessen] {
  cursor: pointer; }
